@-webkit-keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}25%{-webkit-transform:rotate(90deg);transform:rotate(90deg)}50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}75%{-webkit-transform:rotate(270deg);transform:rotate(270deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}25%{-webkit-transform:rotate(90deg);transform:rotate(90deg)}50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}75%{-webkit-transform:rotate(270deg);transform:rotate(270deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.Loading{background-color:rgba(0,0,0,0.65);bottom:0px;left:0px;position:fixed;right:0px;top:0px;z-index:2050}.Loading .Loading-Spinner{-webkit-animation:spin 2s linear infinite;animation:spin 2s linear infinite;border:10px solid #fff;border-radius:50%;border-bottom-color:#fff;border-left-color:transparent;border-right-color:transparent;border-top-color:#fff;height:250px;left:calc(50% - 250px / 2);position:fixed;top:calc(50% - 250px / 2);width:250px}.Loading .progress{border-radius:0px}.Loading::after{content:"Por favor, aguarde...";color:#fff;font-weight:bold;left:calc(50% - 100px);position:fixed;text-shadow:0px 1px 1px #000;text-transform:uppercase;top:calc(50% - 10px)}



