body{background-attachment:fixed;background-image:-webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0,0,0,0.1)));background-image:-webkit-linear-gradient(top, transparent 0%, rgba(0,0,0,0.1) 100%);background-image:-o-linear-gradient(top, transparent 0%, rgba(0,0,0,0.1) 100%);background-image:linear-gradient(to bottom, transparent 0%, rgba(0,0,0,0.1) 100%);background-repeat:no-repeat;background-size:100% 100%;font-family:Gotham;min-height:100%;min-width:100%}::-webkit-scrollbar{display:none}



