.DataGrid{overflow-y:scroll}.DataGrid .table{table-layout:fixed}.DataGrid .table thead{background-color:rgba(255,255,255,0.95)}.DataGrid .table thead th{cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.DataGrid .table thead th.DataGridHeadCellOrder-0:after{content:""}.DataGrid .table thead th.DataGridHeadCellOrder-1:after{content:"▾";-webkit-transform:rotate(180deg);-ms-transform:rotate(180deg);transform:rotate(180deg)}.DataGrid .table thead th.DataGridHeadCellOrder--1:after{content:"▾"}.DataGrid .table thead th.DataGridHeadCellOrder-0:after,.DataGrid .table thead th.DataGridHeadCellOrder-1:after,.DataGrid .table thead th.DataGridHeadCellOrder--1:after{font-size:16px;margin-top:-2px;margin-left:10px;position:absolute}.DataGrid .table tbody td{overflow:hidden;-o-text-overflow:ellipsis;text-overflow:ellipsis;white-space:nowrap}.DataGrid .table.table-striped tbody tr:nth-of-type(even){background-color:rgba(50,200,50,0.05)}.DataGrid .table.table-striped tbody tr:nth-of-type(odd){background-color:#fff}



